import React from "react";
import moment from "moment";

import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  useTheme,
  makeStyles,
  Collapse,
  Box,
  Typography,
} from "@material-ui/core";

import Loader from "../Loader";
import { countryNamesShortSe } from "../../country-names.se";

const useStyles = makeStyles({
  table: {
    // minWidth: 470,
  },
  firstTableRow: {
    "&.Mui-selected, &.Mui-selected:hover": {
      backgroundColor: "#3f51b526",
    },
    cursor: "pointer",
    "& > *": {
      borderBottom: "unset",
    },
  },
  kickoff: {
    textTransform: "capitalize",
  },
});

function Results({ firebase }) {
  const [results, setResults] = React.useState(null);
  const theme = useTheme();

  React.useEffect(() => {
    async function getResults() {
      const get = firebase.functions.httpsCallable("resultsFile");
      const res = await get();
      console.log(res);
      setResults(res.data);
    }
    getResults();
  }, [firebase.functions]);

  const classes = useStyles();
  return !results ? (
    <Loader />
  ) : (
    <TableContainer component={Paper}>
      <Table className={classes.table} size="small">
        <TableHead>
          <TableRow>
            <TableCell colSpan={1}>#</TableCell>
            <TableCell colSpan={6}>Namn</TableCell>
            <TableCell align="center">Totalt</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {results.map((row, i) => (
            <Row row={row} i={i} key={row.name} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
const getSwedishName = (teamName) => {
  const country = (teamName || "").replace(" ", "_");
  return countryNamesShortSe[country];
};
export default Results;

function Row({ row, i }) {
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();

  return (
    <>
      <TableRow
        selected={open}
        hover
        className={classes.firstTableRow}
        onClick={() => setOpen(!open)}
      >
        <TableCell colSpan={1}>
          {i === 0 ? "🥇" : i === 1 ? "🥈" : (i + 1).toString()}
        </TableCell>
        <TableCell colSpan={6}>{row.name}</TableCell>
        <TableCell align="center" colSpan={1}>
          {row.totalPoints}
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell
          colSpan={8}
          style={{
            paddingTop: !open ? "0px" : "initial",
            paddingBottom: !open ? "0px" : "initial",
          }}
        >
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Sammanfattning
              </Typography>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell align="right">{"2 Poäng"}</TableCell>
                    <TableCell align="right">{"1 Poäng"}</TableCell>
                    <TableCell align="right">{"0 Poäng"}</TableCell>
                    <TableCell align="right">Poäng total</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow>
                    <TableCell align="right">{row.fullPoints}</TableCell>
                    <TableCell align="right">{row.correctOutcomes}</TableCell>
                    <TableCell align="right">{row.wrongOutcomes}</TableCell>
                    <TableCell align="right">{row.totalPoints}</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
              <Typography variant="h6" gutterBottom component="div">
                Historik
              </Typography>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell>Match</TableCell>
                    <TableCell>Avspark</TableCell>
                    <TableCell align="right">Tipp</TableCell>
                    <TableCell align="right">Resultat</TableCell>
                    <TableCell align="right">Poäng</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {(row.matches || []).map((historyRow) => (
                    <TableRow key={historyRow.kickoff + historyRow.homeTeam}>
                      <TableCell component="th" scope="row">
                        {`${getSwedishName(
                          historyRow.teams.home
                        )} - ${getSwedishName(historyRow.teams.away)}`}
                      </TableCell>
                      <TableCell className={classes.kickoff}>
                        {`${moment(historyRow.kickoff).format(
                          "dddd DD/MM HH:mm"
                        )}`}
                      </TableCell>
                      <TableCell align="right">
                        {`${historyRow.bet.home} - ${historyRow.bet.away}`}
                      </TableCell>
                      <TableCell align="right">
                        {`${historyRow.result.home} - ${historyRow.result.away}`}
                      </TableCell>
                      <TableCell align="right">{historyRow.points}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  );
}
